}

.breed-select {
  -webkit-appearance: none;
  appearance: none;
  background-size: 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  height: 57px;
  padding: 10px 38px 10px 16px;
  transition: border-color .1s ease-in-out, box-shadow .1s ease-in-out;
}

.breed-select:hover {
  border: 1px solid #999;
}

.breed-select:focus {
  border: 1px solid #999;
  outline: none;
  box-shadow: 0 3px 5px #0003;
}

